<template>
	<section class="shop-item" @click="itemClick">
		<div class="shop-info">
			<div class="shop-logo">
				<img src="https://fuss10.elemecdn.com/f/48/226c6e26d7398ddfd3821f638c2efpng.png?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/">
			</div>
			<div class="shop-main">
				<section>
					<h3>
						<span>这是一个很长很长很长很长很长很长很长很长的标题</span>
					</h3>
				</section>
				<section class="shop-ration-wrap">
					<div class="shop-rating-inner">
						<div class="shop-rating">
							<div class="rating-gray">
								<img src="data:image/svg+xml;base64,">
							</div>
							<div class="rating-actived">
								<img src="data:image/svg+xml;base64,">
							</div>
						</div>
						<span>4.8</span>
						<span>月售1120单</span>
					</div>
					<div class="delivery-mode">
						<span>蜂鸟专送</span>
					</div>
				</section>
				<section class="shop-line">
					<div class="shop-money">
						<span>￥20起送</span>
						<span>远距离配送费￥6.3</span>
					</div>	
	    			<div class="shop-timed">
						<span>2.76km</span>
	    				<span>34分钟</span>
					</div>	
				</section>
			</div>
		</div>
		<div class="shop-activities-wrap">
			<div class="shop-tags">
				<span>西餐</span>
	    		<span>品质联盟</span>
	    		<span>口碑人气好店</span>
			</div>
			<div class="shop-activities">
				<ul>
					<li>
		    			<span>首</span>
		    			<span>新用户下单立减17元</span>
		    		</li>
		    		<li>
		    			<span>减</span>
		    			<span>满100减1，满200减2，满300减3</span>
		    		</li>
		    		<li v-show="showActivitiesList">
		    			<span>折</span>
		    			<span>单品折扣</span>
		    		</li>
		    		<li v-show="showActivitiesList">
		    			<span>食</span>
		    			<span>该商户食品安全已由国泰产险承担，食品安全有保障</span>
		    		</li>
				</ul>
				<div class="shop-activities-btn" @click.stop="showActivitiesList = !showActivitiesList">
					<span>4个活动</span>
					<img src="data:image/svg+xml;base64,">
				</div>
			</div>
		</div>
	</section>
</template>

<script>
export default {
  name: 'ShopItem',
  data () {
    return {
		showActivitiesList:false
    }
  },
  methods : {
	itemClick () {
		this.$router.push({path:`/shop/E123045f5445d505480`});
	}
  }
}
</script>

<style scoped>
.shop-item .shop-info ,
.shop-rating-inner ,
.shop-activities ,
.shop-money ,
.shop-timed ,
.shop-activities ul li ,
.shop-item .shop-main h3 ,
.shop-item section {
	display: -webkit-flex;
	display: -ms-flex;
	display: -webkit-box;
	display: flex;
}
.shop-activities ul ,
.shop-item .shop-main {
	-webkit-flex: 1;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
}
.shop-item .shop-main h3 span,
.shop-activities ul li span:last-child ,
.shop-money span:last-child {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
.shop-ration-wrap {
	-webkit-justify-content:space-between;
	justify-content:space-between;
}
.delivery-mode span {
	color: #fff;
	font-size: 8px;
	display: block;
	transform:scale(.8);
	background-image: -webkit-linear-gradient(45deg,#0085ff,#0af);
    background-image: linear-gradient(45deg,#0085ff,#0af);
}
.shop-line {
	-webkit-justify-content:space-between;
	justify-content:space-between;
}
.shop-money span+span {
	max-width: 120px;	
}
.shop-money span {
	font-size: 8px;
}
.shop-timed span+span:before ,
.shop-money span+span:before {
	margin: 0 2px;
	color: #ddd;
    content:"|";
}
.shop-item {
	padding: 10px;
	border-bottom: 1px solid #eee;/*no*/
}
.shop-item .shop-logo {
	width: 48px;
	height: 48px;
	flex:none;
	-webkit-flex:none;
	-ms-flex:none;
	-webkit-box-flex:none;
}
.shop-item .shop-logo img {
	width: 100%;
	height: 100%;
}
.shop-item .shop-main {
	margin-left: 5px;
	overflow: hidden;
}
.shop-main section {
	color: #666;
	margin-bottom: 5px;
}
.shop-main section:last-child {
	margin-bottom: 0px;
}
.shop-item .shop-main h3 {
	font-size: 12px;
	overflow: hidden;
	color: #000;
}
.shop-rating {
	position: relative;
}
.shop-rating-inner span {
	padding: 0 2px;
}
.shop-rating .rating-actived {
	width: 90%;
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
}

.shop-activities-wrap {
	padding-left: 53px;
	margin-top: 10px;
}

.shop-tags span {
	border:1px solid rgb(221, 221, 221);/*no*/
	border-radius: 2px;
	padding: 0 1px;/*no*/
	color: rgb(102, 102, 102);
}

.shop-activities {
	margin-top: 10px;
}
.shop-activities ul {
	overflow: hidden;
}
.shop-activities ul li {
	padding: 1px 0;/*no*/
}
.shop-activities ul li span:first-child {
	padding: 1px;/*no*/
	border-radius: 2px;
	width: 10px;
	height: 10px;
	line-height: 10px;
	font-size: 8px;
	background-color: green;
	color: #fff;
	text-align: center;
	margin-right: 5px;
	align-items:center;
}
.shop-activities-btn,
.shop-activities ul li span:last-child {
	font-size: 8px;
	color: #666;
}
.shop-activities-btn img {
	width: 6px;
	vertical-align: middle;
}
</style>



